import {api} from '/js/api'
import {Languages, Store} from "../../js/util";

api({
	getcurrentuserInfo: 'user/getcurrentuserInfo',
	GetCurrentUserInfoAndMenus: 'user/GetCurrentUserInfoAndMenus?type=0',
	getSlider: 'canrousel/GetAllCanrousel'
});



const menu = {
	Wishlist: {
		icon: __uri('./assets/ico96-like.png'),
		url: '../wishlist/wishlist.html'
	},
	Schedule: {
		icon:  __uri('./assets/ico96-calendar.png'),
		url: __uri('../schedule/mine.html')
	},
	MessageBox: {
		icon:  __uri('./assets/ico96-chat.png'),
		url: __uri('../msgBox/msgBox.html')
	},
	Exhibitors: {
		icon:  __uri('./assets/ico96-house.png'),
		url: __uri('../wishlist/wishlist.html')
	},
	Buyers: {
		icon:  __uri('./assets/ico96-user.png'),
		url: __uri('../buyer/buyer1.html')
	},
	Events: {
		icon:  __uri('./assets/ico96-bloom.png'),
		url: __uri('../meetings/list.html')
	},
	Navigation: {
		icon:  __uri('./assets/ico96-paper.png'),
		url: __uri('../wishlist/wishlist.html')
	},
	PhotoGallary: {
		icon:  __uri('./assets/ico96-camera.png'),
		url: __uri('../gallery/gallery.html')
	},
	ExhibitionInfo: {
		icon:  __uri('./assets/ico96-paste.png'),
		url: __uri('../wishlist/wishlist.html')
	},
	Press: {
		icon:  __uri('./assets/ico96-media.png'),
		url: __uri('../wishlist/wishlist.html')
	},
	MyScore: {
		icon:  __uri('./assets/ico96-config.png'),
		url: __uri('../wishlist/wishlist.html')
	},
};


type serverMenu = {
	icon: string;
	name: string;
	url: string;
	txt: string;
}

api.GetCurrentUserInfoAndMenus((data) => {
	console.log(data);

	let userMenu = data.menus;

	(userMenu as serverMenu[]).forEach(function (v, i) {
		let m = menu[v.name];
		if (m) {
			v.icon = m.icon;
			v.url = m.url;
			v.txt = Languages.package['menu'][v.name];
		}
		else {
			console.warn(v);
		}
	});


	document.getElementById('board').innerHTML = template('tpl-board', data);

	document.getElementById('spUserName').innerText = Store.get('userName');
	document.getElementById('spRealName').innerText = `${data.firstName} ${data.lastName}`;


});


api.getSlider((data)=> {
	document.getElementById('slider').innerHTML = template('tpl-slider', data);
	mui('#slider').slider();
});

mui('#myScroll').scroll();


console.log(Languages.package['leftMenu']);

document.getElementById('ulLeftMenu').innerHTML = template('tpl-leftMenu', Languages.package);

mui('#swLgn').switch();

document.getElementById('swLgn').addEventListener('toggle', function (event: CustomEvent) {
	Store.set(Languages.configKeyName, Languages.names[Languages.current===Languages.names.cn ? 'en' : 'cn']);
	setTimeout(() => {
		location.href = './index.html?t=' + (new Date()).getTime();
	}, 300);
});


document.getElementById('message').addEventListener('tap', function () {
	location.href = '../announcement/announcement.html';
});


document.getElementById('lmWishList').addEventListener('tap', function () {
	location.href = '../wishlist/result.html';
});

document.getElementById('lmSignOut').addEventListener('tap', function () {
	location.href = '../login/index.html';
});